	module.exports = {"data":{"site":{"siteMetadata":{"title":"Haotian's blog","author":"Haotian Chang"}},"markdownRemark":{"id":"/home/cht/Documents/cht/blog/src/pages/growth-hacking/index.md absPath of file >>> MarkdownRemark","html":"<h2>Sean Ellis talks about the 3 stages of Growth Hacking Success</h2>\n<p><a href=\"http://www.youtube.com/watch?feature=player_embedded&v=i0nxESR9sFs\" target=\"_blank\"><img src=\"http://img.youtube.com/vi/i0nxESR9sFs/0.jpg\" alt=\"IMAGE ALT TEXT HERE\" width=\"240\" height=\"180\" border=\"10\"></a></p>\n<blockquote>\n<p>Growth Pyramid</p>\n</blockquote>\n<ul>\n<li>\n<p>Product/Market Fit </p>\n</li>\n<li>\n<p>Stacking the Odds</p>\n</li>\n<li>\n<p>Scale Growth</p>\n</li>\n</ul>\n<blockquote>\n<p>Testing Drives Growth</p>\n</blockquote>\n<ul>\n<li>Test to discover</li>\n<li>Test to optimize</li>\n</ul>\n<blockquote>\n<p>Unbridled Ideation</p>\n</blockquote>\n<ul>\n<li>Build large backlog of ideas</li>\n<li>Invite participation from whole company</li>\n</ul>\n<blockquote>\n<p>ICE</p>\n</blockquote>\n<p>Impact  Confidence Experiment</p>\n<h2>LinkedIn Head of Growth Aatif Awan - Growth Hacking is Dead. Long Live Growth!</h2>\n<p><a href=\"http://www.youtube.com/watch?feature=player_embedded&v=bqNP0ZGLddc\" target=\"_blank\"><img src=\"http://img.youtube.com/vi/bqNP0ZGLddc/0.jpg\" alt=\"IMAGE ALT TEXT HERE\" width=\"240\" height=\"180\" border=\"10\"></a></p>\n<h3>Startup Success = Product + Growth + Revenue</h3>\n<p>1.Product\nMake something people want</p>\n<p>2.Growth\nGet lots of people to use the product</p>\n<p>3.Revenue\nGet someone to pay for product or for access to users</p>\n<h3>Good product comes first, growth second</h3>\n<ul>\n<li>Investing in growth before you have a good product is a waste of resources</li>\n<li>It might seem to work initially, but reality catches up eventually</li>\n</ul>\n<h3>Good product leads to Good Retention</h3>\n<ul>\n<li>Retention curve for good products flattens out after a few months.</li>\n</ul>\n<h3>You can improve retention by …</h3>\n<ul>\n<li>Getting feedback from exiting users to understand what’sworking and what’s not.</li>\n<li>-</li>\n</ul>","frontmatter":{"title":"Growth Hacking","date":"June 02, 2018"}}},"pathContext":{"slug":"/growth-hacking/","previous":{"fields":{"slug":"/hello-world/"},"frontmatter":{"title":"Hello World"}},"next":null}}

